<template>
  <div>
    <div id="superior">
      <v-breadcrumbs id="items" :items="items" divider=">"></v-breadcrumbs>
    </div>
    
    <v-data-table :headers="headers" :items="desserts">
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Gerenciar Leituras</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>

          <v-spacer></v-spacer>

          <v-dialog v-model="dialog" max-width="700px">
            <template v-slot:activator="{ on }">
              <div class="text-right">
                <!-- caso não haja uma leitura do mês registrada -->
                <v-btn color="#283593" v-if="podeCasdastrar" dark class="mb-2" v-on="on">
                  <v-icon>mdi-table-plus</v-icon>Registrar Nova Leitura
                </v-btn>
                <!-- caso já haja uma leitura do mês registrada -->
                <v-dialog v-else v-model="dialogInfo" width="400">
                  <template v-slot:activator="{ on }">
                    <v-btn color="grey" dark v-on="on">
                      <v-icon>mdi-table-plus</v-icon>Registrar Nova Leitura
                    </v-btn>
                  </template>

                  <v-card>
                    <v-card-title class="headline grey lighten-3">
                      <v-icon class="iconTitulo">mdi-alert-outline</v-icon>Alerta!
                    </v-card-title>
                    <v-card-text>Leitura do mês atual já registrada!</v-card-text>
                    <v-btn color="#283593" width="400" text @click="dialogInfo = false">OK</v-btn>
                  </v-card>
                </v-dialog>
              </div>
            </template>

            <FormularioLeitura :leitura="desserts" />

            <v-btn class="registrar" text @click="save">
              <v-icon>mdi-content-save-move-outline</v-icon>Registrar
            </v-btn>
            <v-btn class="error" @click="dialog = false" style="text-align: center;">
              <v-icon>mdi-close</v-icon>Cancelar
            </v-btn>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="recarregar" @click="initialize">
          <v-icon>mdi-restore-alert</v-icon> Recarregar
        </v-btn>
      </template>
    </v-data-table>
    
  </div>
</template>
<script>
import FormularioLeitura from "../components/FormularioLeitura";
import {isAuthenticated, logout} from "../plugins/auth";

export default {
  components: {
    FormularioLeitura,
  },

  data: () => ({
    logado: isAuthenticated(),
    dialog: false,
    dialogInfo: false,
    items: [
      {
        text: "Home",
        to: "/"
      },
      {
        text: "Gerenciar Leituras",
        to: "/gerenciarLeituras",
        disabled: true
      }
    ],
    headers: [
      {
        text: "Nome",
        align: "left",
        sortable: false,
        value: "Morador.nome"
      },
      { text: "Endereço", value: "endereco" },
      { text: "Ultima Leitura", value: "Leituras[0].valor" }
    ],
    desserts: [],
    podeCasdastrar: false,
    editedIndex: -1
  }),

  watch: {
    dialog(val) {
      val || false;
    }
  },

  created() {
    if(this.logado){
      this.initialize();
     }
    else
      this.$axios.post(logout()).then(this.$router.push({ name: 'login'}))
  },

  methods: {
    async initialize() {
      await this.$axios
        .get("http://localhost:3000/leitura/buscarMoradias")
        .then(res => {
          this.desserts = res.data.casas;
          this.podeCasdastrar = res.data.podeCasdastrar;
        });

      if (this.desserts) {
        this.desserts.forEach(element => {
          if (!element.Leituras.length) {
            element.Leituras.push({ id: "", valor: "0" });
          }
        });
      }
    },

    async save()  {
      confirm(
        "Você confirma os dados? Uma vez cadastrada não há a possibilidade de alteração na leitura."
      );
      await this.$axios
        .post("http://localhost:3000/leitura/salveAll", this.desserts)
        .then(res => console.log(res.data));
      //window.location.reload();
      this.dialog = false;
    },

    editItem(item) {
      this.editedIndex = this.registros;
      this.moradia = Object.assign({}, item);
      this.$axios
        .post("http://localhost:3000/leitura/create", item)
        .then(res => console.log(res.data));
      this.dialog = true;
    },

    informe() {
      this.alert = true;
      this.alerta = "Favor preencher o(s) campo(s) vazio(s).";
    }
  }
};
</script>

<style scoped>
.iconTitulo {
  color: #283593 !important;
}

.iconEdit {
  color: darkorange !important;
}

.iconDelete {
  color: darkred !important;
}

.registrar {
  background-color: #283593 !important;
  color: white;
}

.recarregar {
  color: white !important;
  background-color: #283593 !important;
}
</style>