import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Sobre from '../views/Sobre.vue'
import GerenciarMoradores from '../views/GerenciarMoradores.vue'
import GerenciarMoradia from '../views/GerenciarMoradia.vue'
import Contas from '../views/Contas.vue'
// import GerenciarConsumo from '../views/GerenciarConsumo.vue'
import Login from '../views/Login.vue'
import GerenciarLeitura from '../views/GerenciarLeituras.vue'
import Configuracao from '../views/Configuracao.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/gerenciarMoradores',
    name: 'gerenciarMoradores',
    component: GerenciarMoradores
  },
  {
    path: '/gerenciarLeituras',
    name: 'gerenciarLeitura',
    component: GerenciarLeitura
  },
  {
    path: '/gerenciarMoradia',
    name: 'gerenciarMoradia',
    component: GerenciarMoradia
  },
  {
    path: '/contas',
    name: 'contas',
    component: Contas
  },
  {
    path: '/configuracao',
    name: 'configuracao',
    component: Configuracao
  },
  {
    path: '/sobre',
    name: 'sobre',
    component: Sobre
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
