<template>
  <v-app id="inspire">
    <v-content>
      <v-container class="fill-height">
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="aaa">
              <v-toolbar dark color="#283593" flat>
                <v-divider/><v-toolbar-title>- S I C C<v-icon class="icone">mdi-water-outline</v-icon> -</v-toolbar-title><v-divider/>
          
              </v-toolbar>
              <v-card-text class="conteudoLogin">
                <v-form>
                  <v-row>
                    <v-icon>mdi-account-circle</v-icon>
                  <v-text-field v-model="login" label="Usuario" name="login" type="text"/>
                  </v-row>
                  <v-row>
                    <v-icon>mdi-lock</v-icon>
                  <v-text-field v-model="password" id="password" label="Password" name="password" type="password"/>
                  </v-row>
                </v-form>
              </v-card-text>

              <v-divider/>

              <v-card-actions>
                <v-spacer />
                  <v-btn @click="signin" dark color="#283593">
                    <v-icon>mdi-login</v-icon>Entrar
                  </v-btn>
                <v-spacer />
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import {Login, getToken, isAuthenticated} from "../plugins/auth"

export default {
  props:{
    source: String
  },
  data: () => ({
    
    login: "",
    password: "",

    dictionary: {
      attributes: {
        email: "E-mail Address"
        // custom attributes
      },
      custom: {
        name: {
          required: () => "Name can not be empty",
          max: "The name field may not be greater than 10 characters"
          // custom messages
        }
      }
    }
  }),
  mounted() {},
  methods: {
    async signin() {
      if (this.login && this.password) {
        await this.$axios
          .post("http://localhost:3000/signin", {login:this.login, password:this.password})
          .then(res => Login(res.data.token))
          .then(console.log(getToken()))
      }
      if(isAuthenticated){
        this.$router.push({ name: 'home'}).then(window.location.reload())
      }
    }
  }
};
</script>   

<style scoped>
.aaa{
	opacity:.9;
	background-color: #FFFFFF;
}

.icone{
  margin-top: -5px;
}

.conteudoLogin{
  padding: 30px;
}
</style>