<template>
  <div class="home">
    
    <v-card class="abc">
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title class="headline mb-1"> Sistema de Controle de Consumo - SICCO</v-list-item-title>
          <v-list-item-subtitle>Informações gerais sobre dados no sistema</v-list-item-subtitle>
        </v-list-item-content>

      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn color="#283593" width="75" height="75" title="Cadastrar Morador" v-on="on">
            <v-icon color=white size="50">mdi-account-plus</v-icon>
          </v-btn>
        </template>
        <FormularioMorador :usuario="usuario" />
          <v-btn class="success" text @click="validate">
            <v-icon>mdi-content-save</v-icon> Salvar
          </v-btn>
          <v-btn class="error" @click="dialog = false" style="text-align: center;">
            <v-icon>mdi-close</v-icon>Cancelar
          </v-btn>
    </v-dialog>

    <v-dialog v-model="alert" width="500">
              <v-card>
                <v-card-title class="headline grey lighten-3">
                  <v-icon class="iconTitulo">mdi-alert-outline</v-icon> Alerta!</v-card-title>
                <v-card-text>{{alerta}}</v-card-text>
              </v-card>

              <v-btn class="alerta" @click="alert=false">OK</v-btn>
              
            </v-dialog>

      </v-list-item>
    </v-card>

  <v-row class="linha">
  <!-- <v-col cols="6" sm="3"></v-col> -->
<v-col cols="2" sm="3" md="2"></v-col>
<!-- Consumo total do mês -->
    <v-col cols="6" sm="4">
      <v-hover v-slot:default="{ hover }">
      <v-card :elevation="hover ? 16 : 2" class="cardResponsivo" height="200" width="250" title="Consumo total registrado no sistema.">
        <p class="cartaotitulo"><v-icon color="white">mdi-calendar-month</v-icon><b> Consumo total:</b></p>
        <p class="conteudoCartao">{{consumoTotal}}</p>
        <p class="litros" style="color: #283593;"><v-icon style="color: #283593;">mdi-water</v-icon> Litros</p>
      </v-card>
      </v-hover>
    </v-col>

    <!-- Data da leitura -->
    <v-col cols="6" sm="4">
      <v-hover v-slot:default="{ hover }">
      <v-card :elevation="hover ? 16 : 2" class="cardResponsivo1" height="200" width="250" title="Data de registro da última leitura.">
        <p class="cartaoUltimaLeitura"><b><v-icon color="white">mdi-calendar-check</v-icon> Última leitura:</b></p>
        
        <div class="dataTamanho" v-if="ultimaLeitura != 0"><b>
          <!-- formatar a maior data já registrada no sistema.-->
          {{ultimaLeitura[(8)]+ultimaLeitura[(9)]+"/"+ultimaLeitura[(5)]+ultimaLeitura[(6)]+
          "/"+ultimaLeitura[(0)]+ultimaLeitura[(1)]+ultimaLeitura[(2)]+ultimaLeitura[(3)]}}
         </b></div> 

         <div class="dataTamanho" v-else><h5><b>Não registrada</b></h5></div> 

      </v-card>
      </v-hover>
    </v-col>

  </v-row>

  <v-row class="linha">
  <!-- <v-col cols="6" sm="3"></v-col> -->
<v-col cols="2" sm="3" md="2"></v-col>
<!-- Quantidade de moradores -->
    <v-col cols="6" sm="4">
      <v-hover v-slot:default="{ hover }">
      <v-card :elevation="hover ? 16 : 2" class="cardResponsivo" height="200" width="250" title="Quantidade total de moradores ativos registrados.">
        <p class="cartaotituloQuantidadeMorador"><b><v-icon color="white">mdi-account-supervisor-circle</v-icon> Moradores cadastrados:</b></p>
        <p class="conteudoCartaoQuantidadeMorador">{{desserts}}</p>
        <p class="moradores" style="color:  teal;"><v-icon style="color:  teal;">mdi-account-group</v-icon> Moradores</p>
      </v-card>
      </v-hover>
    </v-col>


<!-- Quantidade de moradias -->
    <v-col cols="6" sm="4">
      <v-hover v-slot:default="{ hover }">
      <v-card :elevation="hover ? 16 : 2" class="cardResponsivo1" height="200" width="250" title="Quantidade total de moradias registradas.">
        <p class="cartaotituloQuantidadeMoradias"><v-icon color="white">mdi-home-circle-outline</v-icon><b> Moradias registradas:</b></p>
        <p class="conteudoCartaoQuantidadeMoradias">{{moradias}}</p>
        <p class="litros" style="color: darkslategrey;"> <v-icon style="color: darkslategrey;">mdi-home-group</v-icon> Moradias</p>
      </v-card>
      </v-hover>
    </v-col>
    
  </v-row>
</div>
</template>

<script>
import FormularioMorador from "../components/FormularioMorador";
import {isAuthenticated} from "../plugins/auth";

export default {
  name: 'home',
  components: {
    FormularioMorador
  },
    data: () => ({
    logado: isAuthenticated(),
    dialog: false,
    headers: [
      {
        text: "Nome",
        align: "left",
        sortable: false,
        value: "nome"
      },
      { text: "Endereço", value: "ecartaoInfondereco" },
      { text: "CPF", value: "cpf" },
      { text: "Matrícula", value: "matricula" },
      { text: "Ações", value: "action", sortable: false }
    ],
    desserts: [],
    moradias: [],
    maiorConsumo: [],
    menorConsumo: [],
    consumoTotal: [],
    ultimaLeitura: [],
    editedIndex: -1,
    usuario: {
      nome: "",
      endereco: "",
      cpf: "",
      matricula: "",
      CasaId: "",
    },
    alert: false,
    alerta: ""
  }),
  
  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      if(this.logado){
      await this.$axios.get('http://localhost:3000/morador/findAllCount')
      .then(res => this.desserts = res.data || '0'),
      this.$axios.get('http://localhost:3000/casa/findAllCount')
      .then(res => this.moradias = res.data || '0'),
      // this.$axios.get('http://localhost:3000/leitura/findMaxConsumo')
      // .then(res => this.maiorConsumo = res.data || '0'),
      // this.$axios.get('http://localhost:3000/leitura/findMinConsumo')
      // .then(res => this.menorConsumo = res.data || '0')
      this.$axios.get('http://localhost:3000/leitura/findConsumoTotal')
      .then(res => this.consumoTotal = res.data || '0')
      this.$axios.get('http://localhost:3000/leitura/findDataLeitura')
      .then(res => this.ultimaLeitura = res.data || '0')
      }
      else
      this.$router.push({ name: 'login'})
    },

    save() {
      if (this.usuario.id) {
         this.$axios.post('http://localhost:3000/morador/update', this.usuario)
        .then(res => console.log(res.data))
        window.location.reload()
      } else {
        this.$axios.post('http://localhost:3000/morador/create', this.usuario)
        .then(res => console.log(res.data))
        window.location.reload()
        this.dialog = false;
      }
    },
    validate() {
      if (
        this.usuario.nome == "" &&
        this.usuario.cpf == "" &&
        this.usuario.CasaId == ""
      ) {
        this.alert = true;
        this.alerta = "Favor preencher o(s) campo(s) vazio(s).";
      } else if (this.usuario.nome == "" && this.usuario.cpf == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (Nome e CPF).";
      } else if (this.usuario.nome == "" && this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (Nome e Endereço).";
      } else if (this.usuario.cpf == "" && this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (CPF e Endereço).";
      } else if (this.usuario.nome == "") {
        this.alert = true;
        this.alerta = "Campo \"Nome\" vazio!";
      } else if (isNaN(this.usuario.cpf)) {
        this.alert = true;
        this.alerta = "Campo \"CPF\" inválido! Favor digitar apenas números.";
      } else if (this.usuario.cpf == "") {
        this.alert = true;
        this.alerta = "Campo \"CPF\" está vazio!";
      } else if (this.usuario.cpf.length < 11) {
        this.alert = true;
        this.alerta = `"CPF" inválido! ${11 - this.usuario.cpf.length} digitos a menos que o minímo necessário.`;
      } else if (this.usuario.cpf.length > 11) {
        this.alert = true;
        this.alerta `CPF inválido! ${this.usuario.cpf.length - 11} digitos a mais que o máximo necessário.`;
      } else if (this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Campo \"Endereço\" está vazio/inválido.";
      } else {
        this.save();
      }
    },
  }
}
</script>

