<template>
  <div>
    <div id="superior">
      <v-breadcrumbs id="items" :items="items" divider=">"></v-breadcrumbs>
    </div>

    <v-data-table :headers="headers" :items="desserts" :search="search" sort-by class="elevation-1">
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Gerenciar Moradores</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>

          <v-spacer></v-spacer><v-spacer></v-spacer>
          <v-text-field v-model="search" width="20px" append-icon="mdi-magnify" label="Pesquisar morador" single-line hide-details></v-text-field>
          <v-spacer></v-spacer><v-spacer></v-spacer>
          
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="#283593" dark class="mb-2" v-on="on">
                <v-icon>mdi-account-plus</v-icon>Cadastrar Morador
              </v-btn>
            </template>
            <FormularioMorador :usuario="usuario" />
            
            <v-dialog v-model="alert" width="500">
              <v-card>
                <v-card-title class="headline grey lighten-3">
                  <v-icon class="iconTitulo">mdi-alert-outline</v-icon> Alerta!</v-card-title>
                <v-card-text>{{alerta}}</v-card-text>
              </v-card>

              <v-btn class="alerta" @click="alert=false">OK</v-btn>
              
            </v-dialog>

            <v-btn class="success" text @click="validate">
              <v-icon>mdi-content-save</v-icon>Salvar
            </v-btn>
            <v-btn class="error" @click="dialog = false" style="text-align: center;">
              <v-icon>mdi-close</v-icon>Cancelar
            </v-btn>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.action="{ item }">
  <v-icon
    class="mr-2 iconEdit"
    title="Editar morador"
    @click="editItem(item)"
  >mdi-account-edit-outline</v-icon>
  <v-icon
    class="iconDelete"
    title="Deletar morador"
    @click="destroy(item)"
  >mdi-account-remove-outline</v-icon>
</template>
  <template v-slot:no-data>
    <v-btn color="recarregar" @click="initialize">
    <v-icon>mdi-restore-alert</v-icon> Recarregar</v-btn>
  </template>
    </v-data-table>
  </div>
</template>

<script>
import FormularioMorador from "../components/FormularioMorador";
import {isAuthenticated} from "../plugins/auth";

export default {
  components: {
    FormularioMorador
  },

  data: () => ({
    logado: isAuthenticated(),
    dialog: false,
    search: '',
    items: [
      {
        text: "Home",
        to: "/"
      },
      {
        text: "Gerenciar Moradores",
        to: "/gerenciarMoradores",
        disabled: true
      }
    ],
    headers: [
      {
        text: "Nome",
        align: "left",
        value: "nome"
      },
      { text: "CPF", value: "cpf" },
      { text: "Endereço", value: "Casa.endereco"},
      { text: "Ações", value: "action", sortable: false }
    ],
    desserts: [],
    editedIndex: -1,
    usuario: {
      id: "",
      nome: "",
      cpf: "",
      CasaId: "",
    },
    alert: false,
    alerta: ""
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Novo Morador" : "Editar Morador";
    }
  },

  watch: {
    dialog(val) {
      val || false;
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      if(this.logado){
      await this.$axios
        .get("http://localhost:3000/morador/findByIdCasa")
        .then(res => (this.desserts = res.data));
      }
      else
        this.$router.push({ name: 'login'})
    },

    async save() {
      if (this.usuario.id) {
        confirm("Confirma a alteração dos dados?") &&
        await this.$axios
          .post("http://localhost:3000/morador/update", this.usuario)
          .then(res => console.log(res.data));
        window.location.reload();
      } else {
        await this.$axios
          .post("http://localhost:3000/morador/create", this.usuario)
          .then(res => console.log(res.data));
        window.location.reload();
        this.dialog = false;
      }
    },

    validate() {
      if (
        this.usuario.nome == "" &&
        this.usuario.cpf == "" &&
        this.usuario.CasaId == ""
      ) {
        this.alert = true;
        this.alerta = "Favor preencher o(s) campo(s) vazio(s).";
      } else if (this.usuario.nome == "" && this.usuario.cpf == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (Nome e CPF).";
      } else if (this.usuario.nome == "" && this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (Nome e Endereço).";
      } else if (this.usuario.cpf == "" && this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Favor preencher os campos: (CPF e Endereço).";
      } else if (this.usuario.nome == "") {
        this.alert = true;
        this.alerta = "Campo \"Nome\" vazio!";
      } else if (isNaN(this.usuario.cpf)) {
        this.alert = true;
        this.alerta = "Campo \"CPF\" inválido! Favor digitar apenas números.";
      } else if (this.usuario.cpf == "") {
        this.alert = true;
        this.alerta = "Campo \"CPF\" está vazio!";
      } else if (this.usuario.cpf.length < 11) {
        this.alert = true;
        this.alerta = `"CPF" inválido! ${11 - this.usuario.cpf.length} digitos a menos que o minímo necessário.`;
      } else if (this.usuario.cpf.length > 11) {
        this.alert = true;
        this.alerta `CPF inválido! ${this.usuario.cpf.length - 11} digitos a mais que o máximo necessário.`;
      } else if (this.usuario.CasaId == "") {
        this.alert = true;
        this.alerta = "Campo \"Endereço\" está vazio/inválido.";
      } else {
        this.save();
      }
    },

    destroy(item) {
      confirm(
        "Você tem certeza que deseja remover este morador? Uma vez removido esta ação não poderá ser desfeita."
      ) &&
        this.$axios
          .post("http://localhost:3000/morador/destroy", item)
          .then(res => console.log(res.data));
      window.location.reload();
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.usuario = Object.assign({}, item);
      this.$axios
        .post("http://localhost:3000/morador/update", item)
        .then(res => console.log(res.data));
      this.dialog = true;
    }
  }
};
</script>

<style scoped>
.iconTitulo {
  color: #283593 !important;
}

.iconEdit {
  color: darkorange !important;
}

.iconDelete {
  color: darkred !important;
}

.alerta{
  color: #283593 !important;
}

.recarregar{
  color: white !important;
  background-color: #283593 !important;
}
</style>