<template>
  <div>
    <div id="superior">
      <v-breadcrumbs id="items" :items="items" divider=">"></v-breadcrumbs>
    </div>

    <v-data-table :headers="headers" :items="desserts" sort-by class="elevation-1">
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Gerenciar Moradias</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>

          <v-spacer></v-spacer>

          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="#283593" dark class="mb-2" v-on="on">
                <v-icon>mdi-home-plus</v-icon>Cadastrar Moradia
              </v-btn>
            </template>
            <FormularioMoradia :moradia="moradia" />

            <v-dialog v-model="alert" width="500">
              <v-card>
                <v-card-title class="headline grey lighten-3">
                  <v-icon class="iconTitulo">mdi-alert-outline</v-icon>Alerta!
                </v-card-title>
                <v-card-text>{{alerta}}</v-card-text>
              </v-card>
              <v-btn class="alerta" @click="alert=false">OK</v-btn>
            </v-dialog>

            <v-btn class="success" text @click="validate">
              <v-icon>mdi-content-save</v-icon>Salvar
            </v-btn>
            <v-btn class="error" @click="dialog = false" style="text-align: center;">
              <v-icon>mdi-close</v-icon>Cancelar
            </v-btn>
          </v-dialog>
        </v-toolbar>
      </template>

      <template v-slot:item.situacao="{ item }">
        <p>{{item.disponivel ? 'Disponivel' : 'Ocupada' }}</p>
      </template>

      <template v-slot:item.action="{ item }">
        <v-icon class="mr-2 iconEdit" title="Editar moradia" @click="editItem(item)">mdi-home-edit</v-icon>
        <v-icon
          v-if="!item.disponivel"
          class="mr-2 iconTitulo"
          title="Desassociar moradia"
          @click="destroy(item)"
        >mdi-home-account</v-icon>
        <v-icon
          v-if="item.disponivel"
          class="iconDelete"
          title="Deletar moradia"
          @click="destroyCasa(item)"
        >mdi-home-remove</v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="recarregar" @click="initialize">
          <v-icon>mdi-restore-alert</v-icon> Recarregar
        </v-btn>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import FormularioMoradia from "../components/FormularioMoradia";
import {isAuthenticated, logout} from "../plugins/auth";

export default {
  components: {
    FormularioMoradia
  },

  data: () => ({
    logado: isAuthenticated(),
    dialog: false,
    items: [
      {
        text: "Home",
        to: "/"
      },
      {
        text: "Gerenciar Moradia",
        to: "/gerenciarMoradia",
        disabled: true
      }
    ],
    headers: [
      { text: "Endereço", value: "endereco" },
      { text: "Hidrômetro", value: "hidrometro" },
      { text: "Situação", value: "situacao" },
      { text: "Ações", value: "action", sortable: false }
    ],
    desserts: [],
    editedIndex: -1,
    moradia: {
      id: "",
      endereco: "",
      hidrometro: ""
    },
    alert: false,
    alerta: ""
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nova Moradia" : "Editar Moradia";
    }
  },

  watch: {
    dialog(val) {
      val || false;
    }
  },
  
  created() {
    if(this.logado){
      this.initialize();
    }
    else
      this.$axios.post(logout()).then(this.$router.push({ name: 'login'}))
  },

  methods: {
    async initialize() {
      if(this.logado){
      await this.$axios
        .get("http://localhost:3000/casa/findAll")
        .then(res => (this.desserts = res.data))
      }
      else
        this.$router.push({ name: 'login'})
    },

    async save() {
      if (this.moradia.id) {
        await this.$axios
          .post("http://localhost:3000/casa/update", this.moradia)
          .then(res => console.log(res.data));
        window.location.reload();
      } else {
        await this.$axios
          .post("http://localhost:3000/casa/create", this.moradia)
          .then(res => console.log(res.data));
        window.location.reload();
        this.dialog = false;
      }
    },

    validate() {
      if (this.moradia.endereco == "" && this.moradia.hidrometro == "") {
        console.log("Os dois campos estão vazios.");
        this.alert = true;
        this.alerta = "Favor preencher o(s) campo(s) vazio(s).";
      } else if (this.moradia.endereco == "") {
        console.log("O campo endereço está vazio.");
        this.alert = true;
        this.alerta = 'Campo "Endereço" vazio!';
      } else if (this.moradia.hidrometro == "") {
        this.alert = true;
        this.alerta = 'Campo "Hidrômetro" vazio!';
      } else if (this.repeatedData()) {
        this.alert = true;
      } else {
        this.save();
      }
    },

    repeatedData() {
      for (var i = 0; i < this.desserts.length; i++) {
        if (this.moradia.endereco == this.desserts[i].endereco) {
          this.alert = true;
          this.alerta = "O endereço informado já existe.";
          return true;
        } else if (this.moradia.hidrometro == this.desserts[i].hidrometro) {
          this.alert = true;
          this.alerta =
            "O hidrômetro informado já existe no endereço " +
            this.desserts[i].endereco;
          return true;
        } else {
          return false;
        }
      }
    },

    destroy(item) {
      confirm(
        "Deseja desvincular a casa ao morador registrado? Uma vez confirmada esta ação não poderá ser desfeita."
      ) &&
        this.$axios
          .post("http://localhost:3000/casa/destroy", item)
          .then(res => console.log(res.data));
      window.location.reload();
    },

    destroyCasa(item) {
      confirm(
        "Você tem certeza que deseja remover esta moradia? Uma vez removida esta ação não poderá ser desfeita."
      ) &&
        this.$axios
          .post("http://localhost:3000/casa/destroyCasa", item)
          .then(res => console.log(res.data));
      window.location.reload();
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.moradia = Object.assign({}, item);
      this.$axios
        .post("http://localhost:3000/casa/update", item)
        .then(res => console.log(res.data));
      this.dialog = true;
    }
  }
};
</script>

<style scoped>
p {
  margin: 0;
}
.iconTitulo {
  color: #283593 !important;
}

.iconEdit {
  color: darkorange !important;
}

.iconDelete {
  color: darkred !important;
}

.alerta {
  color: #283593 !important;
}

.recarregar {
  color: white !important;
  background-color: #283593 !important;
}
</style>