<template>
  <v-card>
    <v-card-title>
      <v-icon class="icon_especial">mdi-account-circle</v-icon>
      {{formTitle}}
    </v-card-title>
    <hr />
    <v-card-text>
      <v-container>
        <v-row>
          <v-icon>mdi-account-edit</v-icon>
          <v-col cols="12" sm="11" md="11">
            <v-text-field v-model="usuario.nome" label="Nome" required></v-text-field>
          </v-col>
          <v-icon>mdi-card-account-details</v-icon>
          <v-col cols="12" sm="11" md="11">
            <v-text-field
              v-model="usuario.cpf"
              label="CPF"
              maxlength="11"
              required
            ></v-text-field>
          </v-col>

          <v-icon>mdi-home</v-icon>
          <v-autocomplete
            v-model="select"
            :loading="loading"
            :items="items"
            :search-input.sync="search"
            cache-items
            class="mx-4"
            flat
            hide-no-data
            hide-details
            label= "Endereço"
            solo-inverted
            :append="idMoradia()"
            required
          ></v-autocomplete>
        </v-row>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "FormularioMorador",
  props: ["usuario", "alert", "alerta"],
  data: () => ({
    formTitle: "Formulário",
    editedItem: {
      id: "",
      nome: "",
      cpf: ""
    },
    editedIndex: -1,
    select: null,
    loading: false,
    items: [],
    search: null,
    desserts: [],
    casas: [],
    CasaId: "",

  }),
  watch: {
    search(val) {
      val && val !== this.select && this.querySelections(val);
    }
  },

  created() {
    this.initialize();
  },
  methods: {
    querySelections(v) {
      this.loading = true;
      // Simulated ajax query
      setTimeout(() => {
        this.items = this.casas.filter(e => {
          return (e || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1;
        });
        this.loading = false;
      }, 500);
    },

    filterName() {
      for (var i = 0; i < this.desserts.length; i++) {
        if (this.desserts[i].disponivel) {
          this.casas.push(this.desserts[i].endereco);
        }
      }
      console.log(this.casas);
    },

    initialize() {
      this.$axios
        .get("http://localhost:3000/casa/findAll")
        .then(res => (this.desserts = res.data));
      setTimeout(this.filterName, 3000);
    },

      idMoradia(){
        for(var i = 0; i<this.desserts.length; i++){
          if(this.desserts[i].endereco == this.select){
            this.CasaId = this.desserts[i].id
            this.usuario.CasaId = this.CasaId
            return this.desserts[i].id
          }else{
            console.log("Id da casa informada não foi encontrado.")
          }
        }
      }
    }
  }
</script>

<style scoped>
.v-card__title,
.icon_especial,
hr {
  color: #283593 !important;
}
</style>