<template>
<div>
    <div id="superior">
        <v-breadcrumbs id="items" :items="items" divider=">"></v-breadcrumbs>
    </div>
      <!-- <template v-slot:top> -->
      
        <v-toolbar flat color="white">
          <v-toolbar-title>Configurações</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>

          <v-spacer></v-spacer>
          
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="#283593" dark class="mb-2" v-on="on">
                <v-icon class="icone">mdi-content-save-outline</v-icon> Atualizar
              </v-btn>
            </template>

            <FormularioConfiguracao :configuracao="configuracao" />

            <v-dialog v-model="alert" width="500">
              <v-card>
                <v-card-title class="headline grey lighten-3">
                  <v-icon class="iconTitulo">mdi-alert-outline</v-icon> Alerta!</v-card-title>
                <v-card-text>{{alerta}}</v-card-text>
              </v-card>

              <v-btn class="alerta" @click="alert=false">OK</v-btn>
              
            </v-dialog>
            
            <v-btn class="registrar" text @click="validate">
              <v-icon class="icone">mdi-content-save-move-outline</v-icon>Registrar
            </v-btn>
            <v-btn class="error" @click="dialog = false" style="text-align: center;">
              <v-icon class="icone">mdi-close</v-icon>Cancelar
            </v-btn>
          </v-dialog>
        </v-toolbar>
      <!-- </template> -->
    <v-row>
      <v-col cols="2" sm="3" md="2"></v-col>
      <v-col cols="12" sm="4">
        <v-hover v-slot:default="{ hover }">
          <v-card :elevation="hover ? 16 : 2" class="mx-auto" height="350" width="275" title="Valor do Metro Cúbico.">
            <p class="metroCubicoTitulo"><v-icon class="icone">mdi-cash-usd-outline</v-icon><b> Metro Cúbico:</b></p>
            <p class="metroCubico">{{metroCubico.VALOR_METRO_CUBICO}}</p>
            <v-divider/>
            <p class="textoMetroCubico">{{textoMetroCubico}}</p>
          </v-card>
        </v-hover>
      </v-col>
      <v-col  cols="12" sm="3">
        <v-hover v-slot:default="{ hover }">
          <v-card :elevation="hover ? 16 : 2" class="mx-auto" height="350" width="275" title="Quantidade de dias para o vencimento das contas.">
            <p class="diasVencimentoTitulo"><v-icon class="icone">mdi-calendar-remove-outline</v-icon><b> Dias para o vencimento:</b></p>
            <p class="diasVencimento">{{vencimentoFatura.VENCIMENTO_FATURA}}</p>
            <v-divider/>
            <p class="textoDiasVencimento">{{textoDiasVencimento}}</p>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {isAuthenticated, logout} from "../plugins/auth";
import FormularioConfiguracao from "../components/FormularioConfiguracao";

export default {
  components: {
    FormularioConfiguracao
  },

  data: () => ({
    logado: isAuthenticated(),
    textoMetroCubico: "O metro cúbico representa o valor da unidade de medida em questão, ou seja, o valor em reais (R$) da unidade metro cúbico utilizado na geração das contas.",
    textoDiasVencimento: "Os dias para o vencimento correspondem a quantidade de dias incrementados em cima da data de geração das contas, delimitando o período de validade da conta.",
    dialog: false,
      items: [
          {
            text: 'Home',
            to: '/'
          },
          {
            text: 'Configurações',
            to: '/configuracao',
            disabled: true,
          }
      ],
    metroCubico: [],
    vencimentoFatura: [],
    configuracao: {
      VALOR_METRO_CUBICO: "",
      VENCIMENTO_FATURA: ""
    },
    alert: false,
    alerta: ""
  }),

  watch: {
    dialog(val) {
      val || false;
    }
  },

  created() {
    if(this.logado){
      this.initialize();
     }
    else
      this.$axios.post(logout()).then(this.$router.push({ name: 'login'}))
  },

  methods: {
    initialize() {
      this.$axios
        .get("http://localhost:3000/configuracao/valor_metro_cubico")
        .then(res => this.metroCubico = res.data || '0')
        this.$axios
        .get("http://localhost:3000/configuracao/vencimento_fatura")
        .then(res => this.vencimentoFatura = res.data || '0')
    },

    async save() {
  if (this.configuracao) {
    confirm("Confirma a alteração dos dados?") &&
    await this.$axios
      .post("http://localhost:3000/configuracao/atualizar_valor_metro_cubico", {valor: this.configuracao.VALOR_METRO_CUBICO})
      .then(res => console.log(res.data)),
    await this.$axios
      .post("http://localhost:3000/configuracao/atualizar_vencimento_fatura", {valor: this.configuracao.VENCIMENTO_FATURA})
      .then(res => console.log(res.data));
    window.location.reload();
    }
  },

  validate() {
      if (
        this.configuracao.VALOR_METRO_CUBICO == "" &&
        this.configuracao.VENCIMENTO_FATURA == ""
      ) {
        this.alert = true;
        this.alerta = "Favor preencher o(s) campo(s) vazio(s).";
      } else if (this.configuracao.VALOR_METRO_CUBICO == "") {
        this.alert = true;
        this.alerta = "Campo \"Valor do Metro Cúbico\" vazio!";
      } else if (this.configuracao.VENCIMENTO_FATURA == "") {
        this.alert = true;
        this.alerta = "Campo \"Vencimento da Fatura\" vazio!";
      } else if (this.configuracao.VALOR_METRO_CUBICO <= 0) {
        this.alert = true;
        this.alerta = "Campo \"Valor do Metro Cúbico\" não pode ser um valor negativo ou igual a 0!";
      } else if (this.configuracao.VENCIMENTO_FATURA <= 0) {
        this.alert = true;
        this.alerta = "Campo \"Valor do Metro Cúbico\" não pode ser um valor negativo ou igual a data de emissão!";
      }  else {
        this.save();
      }
    },
  },
};
</script>

