              <b>Valor do M³:</b>
              {{ currIndex.valorMetroCubico}}
            </p>
          </div>
          <div>
            <p>
              <b>Dia da Leitura Anterior:</b>
              {{ currIndex.diaLeituraAnterior }}
            </p>
          </div>
          <div>
