              {{ currIndex.diaLeituraAtual}}
            </p>
          </div>

          <div>
            <p>
              <b>Vencimento:</b>
              {{ currIndex.vencimentoFatura }}
            </p>
          </div>

          <div>
            <p>
              <b>Total a pagar:</b>
              {{ currIndex.total }}
            </p>
          </div>
        </div>
      </div>
    </div>
    <v-btn color="#283593" dark @click="gerarPdf">
      <v-icon>mdi-file-pdf-outline</v-icon>gerar pdf
    </v-btn>
  </div>
