<script>
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
  name: "GeradorDeContas",
  data() {
    return {
      valorMetro: 7,
      tabela: [],
      currIndex: {},
      dadosMorador: {},
      dadosCasa: {},
      dataHoje: this.getDate()
    };
  },
  methods: {
    async readMoradores() {
      await this.$axios
        .get("http://localhost:3000/leitura/buscarDadosConta", {
          dataNow: Date.now()
        })
        .then(res => {
          this.tabela = res.data;
        });
    },

    async gerarPdf() {
      for (let i = 0; i < this.tabela.length; i++) {
        window.html2canvas = html2canvas;
        this.currIndex = this.tabela[i];
        this.dadosMorador = this.currIndex.Morador;
        this.dadosCasa = this.currIndex.Morador.Casa;
        let elemento = document.getElementById("tabela-pdf");

        var doc = new jsPDF({
          orientation: "p",
          unit: "pt",
          format: "a4"
        });

        doc.setFontSize(12);

        await doc.html(elemento, {
          html2canvas: {
            width: 200
          }
        });
        await doc.save("conta_" + this.currIndex.Morador.nome.toLowerCase());
      }
    },

    getDate() {
      var dataAtual = new Date();
      var mes = "";
      switch (dataAtual.getMonth() + 1) {
        case 1:
          mes = "Janeiro";
          break;
        case 2:
          mes = "Fevereiro";
          break;
        case 3:
          mes = "Março";
          break;
        case 4:
          mes = "Abril";
          break;
        case 5:
          mes = "Maio";
          break;
        case 6:
          mes = "Junho";
          break;
        case 7:
          mes = "Julho";
          break;
        case 8:
          mes = "Agosto";
          break;
        case 9:
          mes = "Setembro";
          break;
        case 10:
          mes = "Outubro";
          break;
        case 11:
          mes = "Novembro";
          break;
        case 12:
          mes = "Dezembro";
          break;
        default:
          mes = "Não identificado";
      }
      return mes + " de " + dataAtual.getFullYear();
    },

    async geraPDFIndividual(conta){
        window.html2canvas = html2canvas;
        this.currIndex = conta;
        this.dadosMorador = this.currIndex.Morador;
        this.dadosCasa = this.currIndex.Morador.Casa;
        let elemento = document.getElementById("tabela-pdf");

        var doc = new jsPDF({
          orientation: "p",
          unit: "pt",
          format: "a4"
        });

        doc.setFontSize(12);

        await doc.html(elemento, {
          html2canvas: {
            width: 200
          }
        });
        await doc.save("conta_" + this.currIndex.Morador.nome.toLowerCase());
    }
  },
  filters: {
    toReal(valor) {
      return Number(valor).toLocaleString("pt-BR", {
        style: "currency",
        currency: "BRL"
      });
    }
  },
  created() {
    this.readMoradores();
  }
};
</script>

